import React, {
  useEffect,
  useState
} from "react";
import {
  useDispatch,
  useSelector
} from "react-redux";
import {
  connect
} from "./redux/blockchain/blockchainActions";
import {
  fetchData
} from "./redux/data/dataActions";
import getProof from "./redux/data/merkleProof";
import {
  chakra,
  Box,
  VStack,
  Center,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalBody,
  useDisclosure,
  Flex,
  Image,
  SimpleGrid,
  HStack,
  Link
} from '@chakra-ui/react';
import Sj from "./components/callConnect";
import HeadOne from "./components/header";
import ConnectPage from "./components/MinterLayout";

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [feedback, setFeedback] = useState("");
  let nftOwned = data.ownNfts;

  const [claimingNft, setClaimingNft] = useState(false);
  const {
    isOpen,
    onOpen,
    onClose
  } = useDisclosure()
  document.body.style = 'background: #F5C141;';
  const queryParams = new URLSearchParams(window.location.search)
  let showHead = queryParams.get("showHead")


  const getNFTs = (_amount) => {
    const proof = getProof(blockchain.account);
    //console.log("proof: ", proof);
    //console.log("proof length: ", proof.length);
    const isWhitelistOn = data.whitelistIsOn;
    //console.log("getNfts whitelist:", isWhitelistOn)
    const proofNotInWhitelist = isWhitelistOn && (Array.isArray(proof) && proof.length === 0);
    const proofIsInWhitelist = isWhitelistOn && (Array.isArray(proof) && proof.length > 0);
    if (proofNotInWhitelist) {
      //alert("WE are not in whitelist pre-sale")
      onClose();
      setFeedback("This is a presale only for Whitelisted addresses. Please come back tomorrow for Public Sale.");
      setClaimingNft(false);
    } else if (!isWhitelistOn || proofIsInWhitelist) {
      let nftOwned = data.ownNfts;

      if (Array.isArray(nftOwned) && nftOwned.length >= 4) {
        setFeedback(
          "You have reached the Wallet limit for minting 2 Rubber Duckz"
        );
        onClose();
        setClaimingNft(false);
      } else {
        claimNFTs(_amount, proof);

      }
    }

  }


  const claimNFTs = (_amount, proof) => {

    setClaimingNft(true);
    //console.log("account: ", blockchain.account);
    //alert("Proof is Right")
    blockchain.smartContract.methods
      .mint(_amount, proof)
      .send({
        gasLimit: "210000",
        to: "0xa5e25b44B01e09b7455851838c76cde68D13e29F",
        from: blockchain.account,
        value: blockchain.web3.utils.toWei((0.09 * _amount).toString(), "ether"),
      })
      .once("error", (err) => {
        //alert("WE have an error")

        onClose();
        console.log(err);
        setFeedback("It seems the transaction was cancelled, please check your balance");
        setClaimingNft(false);

      }).on('receipt', function (receipt) {
        // receipt example
        //alert("WE have a receipt!")

        //console.log(receipt);

      })
      .then((receipt) => {
        //alert("WE have done!")

        onClose();
        setFeedback(
          "Quack Quack! You just minted " + _amount + " Rubber Duckz! Visit Opensea.io to view your randomly generated NFT!"
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });


  };

  const Minter = (props) => {
    const MintSection = (props) => {

      const [contatore, setContatore] = useState(2);
      const total = props.totalSupply;
      return (


        <VStack spacing="0" alignContent="center" justifyContent="center">
          {props.showHead !== "no" && (
            <Box

              alignItems="center"
              justifyContent="center"
            >
              <Link href='https://www.rubberduckz.com/'>
                <Image htmlWidth="100%"
                  htmlHeight="auto"
                  src="https://static.wixstatic.com/media/485d82_fdd318b4086442239ce17c150acdf1b1~mv2.png">

                </Image>
              </Link>
            </Box>
          )}

          <chakra.h2>
            <chakra.span color="white"
              fontSize="6xl"
              textTransform="uppercase"
              fontFamily='wf_e4b1e1e4405343d9ad0296011'
              letterSpacing="wide"
              textAlign={['center']}
              display="block">WELCOME TO</chakra.span>
            <chakra.span
              display="block"
              color="#8f0100"
              fontSize="6xl"
              textTransform="uppercase"
              fontFamily='wf_e4b1e1e4405343d9ad0296011'
              letterSpacing="wide"
              mt={-14}
              textAlign={['center']}
            >
              THE FLOCK
            </chakra.span>
          </chakra.h2>
          {total === 2000 && (
            <chakra.h2
              pr={{ base: 0, lg: 16 }}
              mb={4}
              fontSize="8xl"
              color="#8f0100"
              fontFamily='wf_e4b1e1e4405343d9ad0296011'

              textTransform="uppercase"
            >SOLD OUT</chakra.h2>
          )}
          {total < 2000 && (
            <>
              <chakra.p
                pr={{ base: 0, lg: 16 }}
                mb={4}
                fontSize="lg"
                color="#8f0100"
                fontFamily='lulo-clean-w01-one-bold'
                letterSpacing="wider"
              >
                PLEASE MINT YOUR<br /> RUBBER DUCKZ <br /> PRICE OF 1 NFT IS 0.09ETH
              </chakra.p>
              <HStack maxW='180px' alignItems="center"
                justifyContent="center">

                <chakra.span color="#8f0100"
                  fontSize="6xl"
                  textTransform="uppercase"
                  fontFamily='wf_e4b1e1e4405343d9ad0296011'
                  letterSpacing="wide" onClick={() => {

                    setContatore(1);
                  }}>-</chakra.span>
                <chakra.span color="#8f0100"
                  fontSize="6xl"
                  textTransform="uppercase"
                  fontFamily='wf_e4b1e1e4405343d9ad0296011'
                  letterSpacing="wide" textAlign={['right']}
                >{contatore}</chakra.span>
                <chakra.span color="#8f0100"
                  fontSize="6xl"
                  textTransform="uppercase"
                  fontFamily='wf_e4b1e1e4405343d9ad0296011'
                  letterSpacing="wide" onClick={() => {

                    setContatore(2);
                  }}>+</chakra.span>
              </HStack>
              <Box display="inline-table" alignContent="center" justifyContent="center" textAlign="center">
                <chakra.button
                  onClick={(e) => {
                    e.preventDefault();
                    props.handleClick(contatore);
                  }}
                  w={["full", , "auto"]}
                  display="inline-flex"
                  alignItems="center"
                  justifyContent="center"
                  px={5}
                  py={3}
                  border="solid transparent"
                  fontSize="6xl"
                  textTransform="uppercase"
                  fontFamily='wf_e4b1e1e4405343d9ad0296011'
                  rounded="xl"
                  borderRadius="xl"
                  color="white"
                  bg="#8f0100"
                  _hover={{
                    bg: "brand.50",
                  }}
                >
                  <chakra.span width="180px">MINT</chakra.span>
                </chakra.button>

                <chakra.p
                  pr={{ base: 0, lg: 16 }}
                  mb={4}
                  fontSize="4xl"
                  color="#8f0100"
                  fontFamily='wf_e4b1e1e4405343d9ad0296011'
                  letterSpacing="wider"
                  textTransform="uppercase"
                >{props.totalSupply}/2000</chakra.p>
              </Box>
              <chakra.p
                pr={{ base: 0, lg: 16 }}
                mb={4}
                fontSize="md"
                color="#8f0100"
                fontFamily='lulo-clean-w01-one-bold'
                letterSpacing="wider"
                textTransform="uppercase"
              >{props.feedback}</chakra.p>
            </>
          )}





        </VStack>
      )

    }

    return (
      <SimpleGrid
        columns={{ base: 1, md: 2 }}
        spacing={0}
      >
        <Flex
          direction="column"
          alignItems="center"
          justifyContent="center"
          px={{ base: 4, lg: 20 }}
          py={4}
        >
          <MintSection
            handleClick={props.handleClick}
            totalSupply={props.totalSupply}
            showHead={props.showHead}
            feedback={props.feedback} />
        </Flex>
        <Box
          mt={{ base: "0", md: "-20px" }}
          ml={{ base: "0", md: "-40px" }}
        >
          <Image boxSize={'md'}
            htmlWidth="100%"
            htmlHeight="auto"
            src="https://static.wixstatic.com/media/485d82_3d2a9c62bc6e43c7bc953bec8380439b~mv2.png"



            loading="lazy"
          />
        </Box>
      </SimpleGrid>
    );
  };



  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {

      dispatch(fetchData(blockchain.account));
      nftOwned = data.ownNfts;
      if (Array.isArray(nftOwned) && nftOwned.length >= 2) {
        setFeedback(
          "You have reached the Wallet limit for minting 2 Rubber Duckz"
        );
      }
    }
  };

  useEffect(() => {

    getData();
  }, [blockchain.account]);

  return (

    <Box textAlign="center"
      fontSize="xl"
      bg="#F5C141" >

      {
        blockchain.account === "" ||
          blockchain.smartContract === null ? (<  VStack spacing={
            2
          }
            ai={
              "center"
            } >
            <  ConnectPage mainText=""
              showHead={showHead}
              onClick={
                (e) => {
                  e.preventDefault();
                  dispatch(connect());
                  getData();
                }
              }
            /> </VStack >


        ) : (


          <VStack alignItems="center"
            justifyContent="center" spacing={
              4
            }>

            <Minter handleClick={
              (contatore) => {
                onOpen()
                getNFTs(contatore);
                getData();
              }
            } totalSupply={data.totalSupply}
              showHead={showHead}
              feedback={feedback}></Minter>






            <Modal closeOnOverlayClick={false}
              isOpen={
                isOpen
              }
              onClose={
                onClose
              }
              isCentered size="xl" motionPreset='slideInBottom' >
              <ModalOverlay />
              <ModalContent bg="#8f0100" >

                <ModalBody borderRadius='lg'>
                  <Center bg="#8f0100">
                    <Flex
                      align="center"
                      justify={{ base: "center", md: "space-around", xl: "space-between" }}
                      direction={{ base: "column-reverse", md: "row" }}
                      wrap="no-wrap"
                      minH="70vh"
                      px={8}
                      mb={16}
                    >
                      <VStack >
                        <Image boxSize='300px'
                          objectFit='cover' borderRadius='15px' src="https://static.wixstatic.com/media/485d82_3d2a9c62bc6e43c7bc953bec8380439b~mv2.png"></Image>
                        <chakra.span fontSize="3xl"
                          textTransform="uppercase"
                          fontFamily='wf_e4b1e1e4405343d9ad0296011'
                          letterSpacing="wide" color="white"> MINT IN PROGRESS </chakra.span>
                      </VStack>


                    </Flex>

                  </Center>

                </ModalBody> </ModalContent >
            </Modal>


          </VStack>
        )
      } </Box >
  );
}

export default App;