// log
import store from "../store";

const fetchDataRequest = () => {
  return {
    type: "CHECK_DATA_REQUEST",
  };
};

const fetchDataSuccess = (payload) => {
  return {
    type: "CHECK_DATA_SUCCESS",
    payload: payload,
  };
};

const fetchDataFailed = (payload) => {
  return {
    type: "CHECK_DATA_FAILED",
    payload: payload,
  };
};

function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}



export const fetchData = (account) => {
  return async (dispatch) => {
    dispatch(fetchDataRequest());
    try {
      let name = await store
        .getState()
        .blockchain.smartContract.methods.name()
        .call();
      let whitelistIsOn = await store
        .getState()
        .blockchain.smartContract.methods.onlyWhitelisted()
        .call();
      console.log("whitelistIsOn:", whitelistIsOn);

      let totalSupply = await store
        .getState()
        .blockchain.smartContract.methods.totalSupply()
        .call();
      let cost = await store
        .getState()
        .blockchain.smartContract.methods.cost()
        .call();
      let ownNfts = await store
        .getState()
        .blockchain.smartContract.methods.walletOfOwner(account)
        .call();
      //console.log(ownNfts);
      //await ownNfts.map(getJsonForId);
      //alert(ownNfts[0]);
      dispatch(
        fetchDataSuccess({
          name,
          totalSupply,
          cost,
          ownNfts,
          whitelistIsOn
        })
      );
    } catch (err) {
      console.log(err);
      dispatch(fetchDataFailed("Could not load data from contract."));
    }
  };
};